.photo-content {
  color: var(--basic-color);
}

.photo-content__title {
  line-height: 24px;
  font-weight: 700;
}

.photo-content__likes,
.photo-content__notice {
  font-weight: 300;
  font-size: 12px;
  text-transform: uppercase;
}

.photo-content__likes {
  position: relative;
  font-weight: 400;
  color: var(--basic-red);
  padding-left: 31px;

  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: 50%;
    margin-top: -6px;
    width: 13px;
    height: 12px;
